
Template.__checkName("home");
Template["home"] = new Template("Template.home", (function() {
  var view = this;
  return [ HTML.Raw('<link rel="stylesheet" href="http://netdna.bootstrapcdn.com/font-awesome/4.0.1/css/font-awesome.min.css">\n    '), Spacebars.include(view.lookupTemplate("topnavbarmenu")), "\n    ", Spacebars.include(view.lookupTemplate("multilevelorig")) ];
}));

Template.__checkName("d3graph");
Template["d3graph"] = new Template("Template.d3graph", (function() {
  var view = this;
  return HTML.Raw('<div id="dgraphid"></div>');
}));

Template.__checkName("multilevelorig");
Template["multilevelorig"] = new Template("Template.multilevelorig", (function() {
  var view = this;
  return [ HTML.DIV({
    id: "pushobj"
  }, "\n        ", Spacebars.include(view.lookupTemplate("breadcrumbMenu")), "\n        ", Spacebars.include(view.lookupTemplate("d3graph")), "\n    "), "\n    ", HTML.DIV({
    id: "menu"
  }, "\n        ", HTML.NAV("\n            ", HTML.Raw('<h2><i class="fa fa-reorder"></i>Env</h2>'), "\n            ", HTML.UL("\n                ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("treeItems"));
  }, function() {
    return [ "\n                    ", Spacebars.include(view.lookupTemplate("multilevelorigNodeTemplate")), "\n                " ];
  }), "\n            "), "\n        "), "\n    ") ];
}));

Template.__checkName("multilevelorigNodeTemplate");
Template["multilevelorigNodeTemplate"] = new Template("Template.multilevelorigNodeTemplate", (function() {
  var view = this;
  return HTML.LI({
    id: function() {
      return Spacebars.mustache(view.lookup("id"));
    },
    title: function() {
      return Spacebars.mustache(view.lookup("name"));
    },
    type: function() {
      return Spacebars.mustache(view.lookup("type"));
    },
    clique: function() {
      return Spacebars.mustache(view.lookup("clique"));
    },
    objid: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("_id"), "_str"));
    }
  }, "\n        ", HTML.A({
    href: "#"
  }, HTML.Raw('<i class="fa fa-laptop"></i>'), Blaze.View("lookup:object_name", function() {
    return Spacebars.mustache(view.lookup("object_name"));
  })), "\n        ", Blaze.If(function() {
    return Spacebars.call(view.lookup("hasChildren"));
  }, function() {
    return [ "\n            ", HTML.H2(HTML.I({
      "class": "fa fa-laptop"
    }), Blaze.View("lookup:object_name", function() {
      return Spacebars.mustache(view.lookup("object_name"));
    })), "\n            ", HTML.UL("\n                ", Blaze.Each(function() {
      return Spacebars.call(view.lookup("children"));
    }, function() {
      return [ "\n                    ", Spacebars.include(view.lookupTemplate("multilevelorigNodeTemplate")), "\n                " ];
    }), "\n            "), "\n        " ];
  }), "\n    ");
}));

Template.__checkName("breadcrumbMenu");
Template["breadcrumbMenu"] = new Template("Template.breadcrumbMenu", (function() {
  var view = this;
  return HTML.Raw('<ol class="breadcrumb">\n        <li class="active">All Categories</li>\n    </ol>');
}));

Template.__checkName("topnavbarmenu");
Template["topnavbarmenu"] = new Template("Template.topnavbarmenu", (function() {
  var view = this;
  return HTML.NAV({
    "class": "navbar navbar-inverse navbar-fixed-top"
  }, "\n        ", HTML.DIV({
    "class": "container-fluid"
  }, "\n            ", HTML.DIV({
    "class": "navbar-header"
  }, "\n                ", HTML.Raw('<button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar" aria-expanded="false" aria-controls="navbar">\n                    <span class="sr-only">Toggle navigation</span>\n                    <span class="icon-bar"></span>\n                    <span class="icon-bar"></span>\n                    <span class="icon-bar"></span>\n                </button>'), "\n                ", HTML.A({
    "class": "navbar-brand",
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), Spacebars.kw({
        route: "homePage"
      }));
    }
  }, "Project OSDNA"), "\n                ", HTML.DIV({
    "class": "navbar-form navbar-left"
  }, "\n                    ", Spacebars.include(view.lookupTemplate("envForm")), "\n                "), "\n            "), "\n            ", HTML.DIV({
    id: "navbar",
    "class": "navbar-collapse collapse"
  }, "\n                ", HTML.UL({
    "class": "nav navbar-nav navbar-right"
  }, "\n                    ", HTML.LI(HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), Spacebars.kw({
        route: "home"
      }));
    }
  }, "Graph")), "\n                    ", HTML.Raw('<li><a href="#">Settings</a></li>'), "\n                    ", HTML.Raw('<li><a href="#">Profile</a></li>'), "\n                    ", HTML.Raw('<li><a href="#">Help</a></li>'), "\n                "), "\n                ", HTML.Raw('<div class="navbar-form navbar-right">\n                    <input type="text" id="search" class="form-control" placeholder="Search...">\n                </div>'), "\n            "), "\n        "), "\n    ");
}));

Template.__checkName("envForm");
Template["envForm"] = new Template("Template.envForm", (function() {
  var view = this;
  return HTML.FORM({
    id: "selEnvForm"
  }, "\n        ", HTML.SELECT({
    "class": "form-control",
    id: "envList"
  }, "\n            ", HTML.Raw("<option>Please Select Env</option>"), "\n            ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("envList"));
  }, function() {
    return [ "\n                ", HTML.OPTION(HTML.Attrs({
      value: function() {
        return Spacebars.mustache(view.lookup("name"));
      }
    }, function() {
      return Spacebars.attrMustache(view.lookup("selected"));
    }), Blaze.View("lookup:name", function() {
      return Spacebars.mustache(view.lookup("name"));
    })), "\n            " ];
  }), "\n        "), "\n    ");
}));
